import React from 'react';
import Layout from '../../components/Layout';
import { Popup } from '../../components/Popup';
import $ from "jquery";


const Detail = (props) => {

  $(document).ready(function(){
    $('.open-floating').on('click', function(){
      if($('.floating-wrap').hasClass('active')){
        $('.floating-wrap, .open-floating').removeClass('active');
      } else {
        $('.floating-wrap, .open-floating').addClass('active');
      }
    });


    //
    $('.floating-wrap .btn-delete').on('click', function(){
      $('.popup-challenge-delete').show();
    });

    //
    $('.floating-wrap .btn-revise').on('click', function(){
      $('.popup-word-revise').show();
    });

    //
    $('.floating-wrap .btn-retry').on('click', function(){
      $('.popup-retry').show();
    });


  });


  return (
		<Layout header={{title:' ', Backbtn:true, link:'/Challenge/Result'}} containName='result'>


      <Popup popupName='popup-challenge-delete' btn confirmtext=''>
         <br/>
        ?
      </Popup>

      <Popup popupName='popup-word-revise' btn confirmtext=''>
         <br/>
        ?
      </Popup>

      <Popup popupName='popup-retry' btn confirmtext=''>
         <br/>
          ?
      </Popup>

      <div className="select-wrap">
        <div className="select">
          <button type="button"></button>
          <ul>
            <li className="active"></li>
            <li></li>
            <li></li>
          </ul>
        </div>
      </div>

      <ul className="result-view">
        <li>
          <div className="english">exist</div>
          <div className="korean"><p className="wrong"></p></div>
          <button type="button" className="btn-sound"> </button>
        </li>
        <li>
          <div className="english">extreme<p className="wrong">extremes</p></div>
          <div className="korean"></div>
          <button type="button" className="btn-sound"> </button>
        </li>
        <li>
          <div className="english">original<p className="answer">ORIGINAL</p></div>
          <div className="korean"></div>
          <button type="button" className="btn-sound"> </button>
        </li>
        <li>
          <div className="english">soon a after</div>
          <div className="korean"><p className="answer"></p></div>
          <button type="button" className="btn-sound"> </button>
        </li>
        <li>
          <div className="english">be pleased width</div>
          <div className="korean">~ <p className="wrong"></p></div>
          <button type="button" className="btn-sound"> </button>
        </li>
        <li>
          <div className="english">put aside</div>
          <div className="korean">~ <p className="answer">~ </p></div>
          <button type="button" className="btn-sound"> </button>
        </li>
        <li>
          <div className="english">critical<p className="answer">criticalnal</p></div>
          <div className="korean"></div>
          <button type="button" className="btn-sound"> </button>
        </li>
        <li>
          <div className="english">mistakenly<p className="answer"></p></div>
          <div className="korean">()</div>
          <button type="button" className="btn-sound"> </button>
        </li>
      </ul>

      <button type="button" className="open-floating"></button>

      <div className="floating-wrap">
        <div className="dim-layer"></div>
        <ul className="btn-list">
          <li>
            <button type="button" className='btn-delete'><i className="icon-delete"></i></button>
          </li>
          <li>
            <button type="button" className='btn-revise'><i className="icon-revise"></i></button>
          </li>
          <li>
            <button type="button" className='btn-retry'>  <i className="icon-retry"></i></button>
          </li>
        </ul>
      </div>
    
  </Layout>
  
  );
};

export default Detail;