import React, { useEffect } from 'react';
import Layout from '../../components/Layout';
import Layer from '../../components/Layer';
import Policy01 from '../../components/Policy01';
import Policy02 from '../../components/Policy02';

import $ from "jquery";

const Join = (props) => {

  //
  function policy01() {
    $('.policy01').show();
  }

  //
  function policy02() {
    $('.policy02').show();
  }

  useEffect(() => {
    //select 
    $('.select button').text($('.select ul li:first-child').text());
  
    $('.select button').on('click', function(){
      var selectUl = $(this).next('ul')
      if(selectUl.is(':visible')) {
        selectUl.hide();
      } else {
        selectUl.show();
      }
    });
  
    $('.select li').on('click', function(){
      var selectText = $(this).text();
      $(this).parent('ul').hide().siblings('button').text(selectText);
  
      if($('.select button').text() === '') {
        $('#email_direct').show().focus();
      } else {
        $('#email_direct').hide().blur();
      }
    });

  }, []);


  return (
		<Layout header={{title:' ', Backbtn:true, link:'/'}} containName='account'>

      <Layer layerName={'policy01'} title={''}>
        <Policy01/>
      </Layer>

      <Layer layerName={'policy02'} title={''}>
        <Policy02/>
      </Layer>

      <div className='join'>
        <div className="input-group">
          <div className="input-wrap">
            <label htmlFor="name_family"></label>
            <input type="text" id="name_family" placeholder=""/>
          </div>
          <div className="sign">/</div>
          <div className="input-wrap">
            <label htmlFor="name_given"></label>
            <input type="text" id="name_given" placeholder=""/>
          </div>
        </div>

        <div className="input-wrap">
          <label htmlFor="email"></label>
          <div className="input-group">
            <div className="input-wrap">
              <input type="text" id="email" placeholder=""/>
            </div>
            <div className="sign">@</div>
            <div className="input-wrap">
              <div className="select">
                <button type="button"></button>
                <ul>
                  <li>naver.com</li>
                  <li>hanmail.net</li>
                  <li>daum.net</li>
                  <li>kakao.com</li>
                  <li>gmail.com</li>
                  <li></li>
                </ul>
              </div>
            </div>
          </div>
          <input type="text" id="email_direct" placeholder=" "/>
        </div>

        <div className="input-wrap">
          <label htmlFor="pw01"></label>
          <input type="password" id="pw01" placeholder="********"/>
          <p className="text-guide text-colred">+  6 .</p>
        </div>

        <div className="input-wrap">
          <label htmlFor="pw02"> </label>
          <input type="password" id="pw02" placeholder="********"/>
          <p className="text-guide text-colred"> .</p>
        </div>

        <div className="input-wrap">
          <label></label>
          <div className="check-group">
            <div className="input-check">
              <input type="checkbox" id="all_agree"/>
              <label htmlFor="all_agree"> </label>
            </div>
            <div className="input-check">
              <input type="checkbox" id="policy01"/>
              <label htmlFor="policy01">  ()</label>
              <button type="button" className="btn-agree" onClick={policy01}><span className="visuallyhidden"></span></button>
            </div>
            <div className="input-check">
              <input type="checkbox" id="policy02"/>
              <label htmlFor="policy02" className="text-col68">  ()</label>
              <button type="button" className="btn-agree" onClick={policy02}><span className="visuallyhidden"></span></button>
            </div>
          </div>
        </div>

        <button type="button" className="btn"></button>
      </div>

		</Layout>
  );
};

export default Join;