import React from 'react';
import Layout from '../components/Layout';
import $ from "jquery";


//active
$(document).ready(function(){
  $('.emotion-wrap button').on('click', function(){
    $(this).addClass('active');
    $(this).parent('li').siblings().children('button').removeClass('active');
  });
})


const Comment = (props) => {
  return (
		<Layout header containName='comment'>
      <div className='comment-top'>
        <div className='comment-text'>
          <h2>  !</h2>
          <p>   ?<br/>
          ,       ?<br/>
            .</p>
        </div>

        <ul className='emotion-wrap'>
          <li>
            <button type='button' className='btn-bad'>  </button>
          </li>
          <li>
            <button type='button' className='btn-soso'>  </button>
          </li>
          <li>
            <button type='button' className='btn-good'></button>
          </li>
        </ul>
      </div>

      <div className='comment-textarea'>
        <strong className='title'>   </strong>
        <textarea placeholder='    .'></textarea>
      </div>

      <div className='btn-bottom'>
        <button type='button' className='btn'></button>
      </div>

		</Layout>
  );
};

export default Comment;